<template>
    <div class="d-flex principal">
        <div class="lateral1">
            <!-- teste -->
        </div>
        <div class="meio w-100 d-flex flex-column">
            <div class="conversor d-flex flex-column justify-content-center">
                <div class="icone mb-4">
                    <img src="../storage/bitcoin.png" width="200" alt="">
                </div>
                <div class="dados d-flex justify-content-center">
                    <span class="d-flex container-input">
                        <span class="simbolo mx-2">QTD</span>
                        <input 
                            class="input-val px-2 text-end" 
                            v-model="quantidade"
                            @focusout="FocusOut"
                        >
                    </span>
                    <span style="font-size: 15px;" class="igualdade mx-2 align-self-center">igual </span>
                    <span class="d-flex container-input">
                        <span class="simbolo mx-2">R$</span>
                        <input 
                            disabled
                            class="input-val px-2 text-end" 
                            v-model="valorBTC"
                        >
                    </span>
                </div>
            </div>
            <div class="text-start">
                <div style="width: 500px;" class="mx-2">
                    <h2 >Preo mdio do BTC nos ltimos 30 dias</h2>
                    <div id="chart">
                        <apexchart type="area" height="350" :options="chartOptions" :series="series"></apexchart>
                    </div>
                    <h3>Valor do BTC agora</h3>
                    <p>Bitcoin  uma forma de dinheiro digital descentralizado, criado em 2009 por um indivduo ou grupo de pessoas usando o pseudnimo Satoshi Nakamoto. Ele revolucionou a forma como pensamos sobre dinheiro e transaes financeiras, oferecendo uma alternativa ao sistema financeiro tradicional controlado por governos e instituies financeiras.</p>

                    <p>Em vez de depender de uma autoridade central, como um banco central, para emitir e regular a moeda, o Bitcoin opera em uma rede peer-to-peer, onde as transaes so verificadas e registradas por uma rede de computadores distribudos em todo o mundo. Isso significa que o Bitcoin  imune  interferncia governamental e  manipulao poltica.</p>

                    <p>Uma das caractersticas mais notveis do Bitcoin  sua oferta limitada. Ao contrrio das moedas fiducirias, que podem ser impressas indefinidamente, o Bitcoin tem um limite mximo de 21 milhes de unidades. Isso confere ao Bitcoin uma escassez intrnseca, semelhante a metais preciosos como o ouro, o que pode potencialmente proteger seu valor contra a inflao.</p>

                    <p>Alm disso, o Bitcoin oferece segurana e privacidade aos seus usurios por meio de tcnicas criptogrficas avanadas. As transaes so registradas em um livro-razo pblico chamado blockchain, que  imutvel e transparente. Isso significa que as transaes de Bitcoin so seguras e no podem ser alteradas ou censuradas.</p>

                    <p>O Bitcoin tambm est se tornando cada vez mais aceito como forma de pagamento em todo o mundo. Desde empresas de tecnologia at varejistas e prestadores de servios, muitos esto comeando a aceitar Bitcoin como uma opo de pagamento legtima.</p>

                    <p>No entanto,  importante notar que o Bitcoin ainda  uma tecnologia em evoluo e enfrenta desafios, como escalabilidade e volatilidade de preos. No entanto, muitos acreditam que o Bitcoin tem o potencial de revolucionar no apenas o sistema financeiro, mas tambm a forma como interagimos com o dinheiro e o valor.</p>

                    <p>Se voc est interessado em explorar o mundo emocionante do Bitcoin, h uma abundncia de recursos disponveis para ajud-lo a comear. De carteiras digitais a exchanges de criptomoedas, h muitas maneiras de comprar, vender e armazenar Bitcoin de forma segura e conveniente.</p>

                    <p>Junte-se  revoluo do dinheiro digital e descubra o que o Bitcoin pode oferecer a voc!</p>

                </div>
            </div>
        </div>
        <div class="lateral2">
            <!-- teste -->
        </div>
    </div>
</template>

<script>

import Axios from '../services/axiosmain'

export default {
    name: 'bitcoinHoje',
    data(){
        return {
            valorBTC: 1,
            quantidade: 1,
            hover: 'false',

            series: [],
        
            chartOptions: {},
        }
    },

    mounted(){
        this.priceBTC()
    },

    components: {
    },

    methods:{
        priceBTC(){
            this.historyBTC()
            Axios.priceBTC()
            .then(respost =>{
                this.valorBTCoriginal = respost.bitcoin.brl
                this.valorBTC = (this.valorBTCoriginal * this.quantidade).toLocaleString('pt-br', {minimumFractionDigits: 2})
            })
        },

        historyBTC(){
            Axios.historyBTC()
            .then(respost =>{
                var date = []
                var valor = []
                respost.prices.forEach(element => {
                    date.push(new Date(element[0]).getDate() + "/" + (new Date(element[0]).getMonth() + 1)+ "/" + new Date(element[0]).getFullYear())
                    valor.push(element[1])
                });

                console.log(valor)

                // var label = []
                // var data = []
                // for (let i = 0; i < dados.length; i++) {
                //     label.push(dados[i][0])
                //     data.push(dados[i][1])
                // }
                this.series = [{
                    name: "Desktops",
                    data: valor
                }],

                this.chartOptions = {
                    chart: {
                        height: 350,
                        type: 'line',
                        zoom: {
                            enabled: false
                        }
                    },
                    dataLabels: {
                        enabled: false
                    },
                    stroke: {
                        curve: 'straight'
                    },
                    title: {
                        text: 'Product Trends by Month',
                        align: 'left'
                    },
                    grid: {
                        row: {
                            colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                            opacity: 0.5
                        },
                    },
                    xaxis: {
                        categories: date,
                    }
                }
                
            })
        },

        FocusOut(){
            if(this.quantidade == ''){
                this.quantidade = 1
            }
        }
    },

    computed:{
        
    },

    watch:{
        quantidade: function() {
            this.valorBTC = (this.valorBTCoriginal * this.quantidade).toLocaleString('pt-br', {minimumFractionDigits: 2})
        }
    }
}
</script>

